import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

export default new Vuex.Store({
  // vuex中的数据源，我们需要保存的数据就保存在这里
  state: {
    userId: 'abc',
    addnum:1
  },
  mutations: {
    changeUserId(state, payload) {
      state.userId = payload
    },
    changeNum(state){
      state.addnum=state.addnum+1
    }
  },
  actions: {
    saveUserId({ commit }, payload) {
      commit('changeUserId', payload)
    },
    updtaeNum(context){
      context.commit("changeNum")
    }
  },
  modules: {
  }
})
