<template>
  <div class="purchase">
    <div class="itop itopp">
      <div id="look">社区</div>
      <a class="van-icon van-icon-plus" @click="toCom"></a>
    </div>
    <div class="pmiddle">
      <div class="dot" v-for="item in allList" :key="item.id">
        <div class="name">{{item.userName}}</div>
        <div class="body">{{item.content}}</div>
        <div class="liker">
          <div>{{item.date}}</div>
          <i class="iconfont icon-xinfensi"></i>
        </div>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
.purchase {
  width: 100%;
  padding-top: 1.5rem;
  padding-bottom: 1rem;
  > .itopp {
    position: fixed;
    top: 0;
    #look {
      color: #fff;
      font-size: 0.6rem;
      line-height: 1.5rem;
    }
    > .van-icon-plus {
      position: absolute;
      right: 0.3rem;
      top: 0rem;
      font-size: 0.6rem;
      line-height: 1.6rem;
      color: #fff;
    }
  }
  .pmiddle {
    padding: 0.3rem;
    > .dot {
      border: #e79191 0.02rem solid;
      border-radius: 0.4rem;
      margin-bottom: 0.5rem;
      padding: 0.2rem;
      padding-bottom: 0.2rem;
      > .name {
        text-align: left;
        font-size: 0.32rem;
        color: brown;
        line-height: 0.4rem;
      }
      > .body {
        line-height: 0.4rem;
        text-align: left;
        font-size: 0.3rem;
      }
      > .liker {
        display: flex;
        justify-content: space-between;
        > div {
          height: 0.4rem;
          line-height: 0.4rem;
          font-size: 0.24rem;
        }
        > i {
          font-size: 0.4rem;
          color: #e79191;
        }
      }
    }
  }
}
</style>
<script>
export default {
  data() {
    return {
      allList: [],
      userid: localStorage.getItem("userid")
    };
  },

  methods: {
    getAll() {
      let that = this;
      this.$axios({
        url: "https://tang1.top/qimimi/getStatmentList?pageNumber=1",
        method: "get"
      }).then(res => {
        if (res.data.status == 200) {
          that.allList = res.data.data.statmentRes;
        }
      });
    },
    toCom() {
      this.$router.push("/comment");
    }
  },
  mounted() {
    this.getAll();
  }
};
</script>