<template>
  <div class="comment">
    <div class="iconfont icon-l-back comment_l" @click="to_pur">返回</div>
    <div class="comment_m">
      <textarea name id cols="30" rows="10" placeholder="写下你今天的心情吧" v-model="comment"></textarea>
    </div>
    <div class="comment_r" @click="send">发表</div>
  </div>
</template>
<style lang="scss">
.comment {
  width: 100%;
  > .comment_l {
    height: 1.5rem;
    line-height: 1.5rem;
    background: #e79191;
    text-align: left;
    padding-left: 0.4rem;
    box-sizing: border-box;
    color: #fff;
    font-size: 0.6rem;
  }
  > .comment_m {
    margin-top: 0.3rem;
    textarea {
      width: 98%;
      margin: 0 auto;
      box-sizing: border-box;
      padding: 0.2rem;
      height: 2rem;
      resize: none;
      border: none;
    }
  }
  > .comment_r {
    margin: 0.3rem auto;
    width: 1.2rem;
    height: 0.8rem;
    line-height: 0.8rem;
    font-size: 0.35rem;
    color: #fff;
    background: #e79191;
    border-radius: 0.16rem;
  }
}
</style>
<script>
export default {
  name: "sort",
  data() {
    return {
      comment: "",
      userid: localStorage.getItem("userid")
    };
  },
  created() {},
  methods: {
    to_pur() {
      this.$router.push("/purchase");
    },
    send() {
      let that = this;
      that
        .$axios({
          url: "https://tang1.top//qimimi/addNewStatment",
          method: "post",
          params: {
            description: that.comment,
            userId: that.userid
          }
        })
        .then(res => {
          if (res.status == 200) {
            that.$toast.success("发表成功")
          }else{
              that.$toast.fail("好像网络开小差了哦")
          }
        });
    }
  }
};
</script>