<template>
  <div class="file">
    <div class="iconfont icon-l-back file_l" @click="to_index">返回</div>
    <div class="file_m">
      <el-upload
        class="upload-demo"
        multiple
        :limit="1"
        :on-exceed="handleExceed"
        action="https://jsonplaceholder.typicode.com/posts/"
        :on-preview="handlePreview"
        :on-remove="handleRemove"
        :file-list="fileList"
        list-type="picture"
        :http-request="HandleHttpRequest"
      >
        <el-button size="small" type="primary">点击上传</el-button>
        <div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过500kb</div>
      </el-upload>
    </div>
  </div>
</template>
<style lang="scss">
.file {
  width: 100%;
  > .file_l {
    height: 1.8rem;
    line-height: 1.8rem;
    background: #e79191;
    text-align: left;
    padding-left: 0.4rem;
    box-sizing: border-box;
    color: #fff;
    font-size: 0.6rem;
  }
}
</style>
<script>
export default {
  data() {
    return {
      fileList: [],
      imgList: []
    };
  },
  methods: {
    to_index() {
      this.$router.push("/index");
    },
    handleChange(value) {
      console.log(value);
    },
    handleRemove(file, imgList) {
      console.log(file, imgList);
      this.imgList = [];
      for (let i = 0; i < imgList.length; i++) {
        this.imgList.push(imgList[i].url);
      }
      console.log(this.imgList);
    },
    handlePreview(file) {
      console.log(file);
    },
    handleExceed(files, fileList) {
      this.$message.warning(
        `当前限制选择 1 个文件，本次选择了 ${
          files.length
        } 个文件，共选择了 ${files.length + fileList.length} 个文件`
      );
    },
    HandleHttpRequest(a) {
      console.log(a.file);
      var formData = new FormData();
      formData.append("file", a.file);
      this.$axios({
        url:
          "http://www.shenzepengzuishuai.cn:8080/suyouwechat-provider-1.0.1/bigFile.action",
        method: "post",
        data: formData
      }).then(res => {
        console.log(res);
        if (res.data.status == 200) {
          this.imgList.push(res.data.data.imgUrl);
        }
        console.log(this.imgList);
        console.log(this.fileList);
      });
    }
  }
};
</script>