<template>
  <div class="index">
    <van-dialog v-model="show1" title="修改当前周" show-cancel-button :show-confirm-button="false">
      <div class="dia_content">
        <div class="dia_content_l dia_contents" @click="weekCan">-</div>
        <div class="dia_content_c dia_contents">{{idss}}</div>
        <div class="dia_content_r dia_contents" @click="weekPlus">+</div>
        <div class="con_boo" @click="setCurrentWeek">确认</div>
      </div>
    </van-dialog>
    <div class="itop">
      <div class="ititle1">
        <!-- <div class="top_upload" @click="to_file">上传</div> -->
        <div class="ititle_left">
          <i class="iconfont icon-l-back" @click="lastWeek"></i>
        </div>

        <h1>第{{this.id}}周</h1>
        <div class="ititle_right">
          <i class="iconfont icon-l-enter" @click="nextWeek"></i>
        </div>
      </div>
    </div>

    <div class="imiddle">
      <div class="ititles">
        <div class="ititle3">
          <div>
            <i class="iconfont icon-bianji" @click="showPopup1"></i>
          </div>
        </div>

        <div class="ititle4">
          <div class="item" v-for="item in timer" :key="item.id">
            <div class="iweek">
              <div class="ismalltop">{{item.zhou|date}}</div>
              <div class="ismallbottom">{{item.month}}/{{item.riqi}}</div>
            </div>
          </div>
        </div>
      </div>

      <div class="ibody">
        <div class="iclass">
          <div v-for="index in classlist" :key="index.id">{{index.number}}</div>
        </div>
        <div class="iclasses">
          <div class="iday" v-for="(item,index) in classes" :key="index">
            <div
              class="ieveryday"
              v-for="(items,index) in item"
              :key="index"
              @click="showPopup(items)"
            >
              <div class="ieverydays" :style="{backgroundColor:(items!=null?'#'+items.color:'')}">
                <div class="tt">{{items!=null?items.lessonName:''}}</div>
                <div class="mm">{{items!=null?items.teacher:''}}</div>
                <div class="bb">{{items!=null?items.location:''}}</div>
              </div>
            </div>
            <van-popup
              v-model="show"
              get-container="body"
              round
              closeable
              bind:close="onClose"
              close-icon-position="top-left"
            >
              <update :parentid="toSonData"></update>
            </van-popup>
          </div>
        </div>
        <add></add>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
.van-dialog__header {
  font-size: 0.5rem;
  font-weight: 600;
  color: black;
}
.van-dialog__content {
  margin-top: 0.4rem;
}
.dia_content {
  width: 100%;
  display: flex;
  padding: 0 1.7rem;
  margin-bottom: 0.5rem;
  > .dia_contents {
    box-sizing: border-box;
    width: 0.8rem;
    height: 0.6rem;
    line-height: 0.6rem;
    border: 1px solid #a59999;
    font-size: 0.5rem;
    color: black;
  }
  .dia_content_l {
    border-top-left-radius: 0.1rem;
    border-bottom-left-radius: 0.1rem;
  }
  .dia_content_r {
    border-top-right-radius: 0.1rem;
    border-bottom-right-radius: 0.1rem;
  }
  .dia_content_c {
    width: 0.8rem;
    margin: 0 0.15rem;
  }
  > .con_boo {
    margin-left: 0.3rem;
    // background: linear-gradient(to right, #4bb0ff, #6149f6);
    background: #e79191;
    width: 1rem;
    height: 0.6rem;
    line-height: 0.6rem;
    color: white;
    border-radius: 0.2rem;
  }
}
.itop {
  background: #e79191;
  height: 1.5rem;
  width: 100%;
  position: fixed;
  box-sizing: border-box;
  color: #fff;
  top: 0;
  z-index: 2;
  .ititle1 {
    width: 100%;
    height: 1.5rem;
    .top_upload {
      position: absolute;
      top: 0.3rem;
      left: 0.2rem;
    }
    h1 {
      line-height: 1.5rem;
      font-size: 0.6rem;
      font-weight: 400;
    }
    > .ititle_left {
      width: 20%;
      height: 1.5rem;
      position: absolute;
      left: 1.2rem;
      i {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 0.6rem;
      }
    }
    > .ititle_right {
      width: 20%;
      height: 1.5rem;
      position: absolute;
      top: 0;
      right: 1.2rem;
      i {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        font-size: 0.6rem;
      }
    }
  }
  .ititle2 {
    height: 0.4rem;
    position: relative;
    h6 {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      font-size: 0.25rem;
    }
  }
  .plus {
    position: absolute;
    top: 0;
    right: 0.3rem;
    font-size: 0.6rem;
    line-height: 1.5rem;
    z-index: 1;
    .van-icon-plus {
      color: #fff;
    }
  }
}
.index {
  width:100%;
  padding-top: 1.5rem;
  padding-bottom: 1.3rem;
  .imiddle {
    .ititles {
      display: flex;
      border-top: 0.01rem solid #cecece;
      border-bottom: 0.01rem solid #cecece;
      .ititle3 {
        flex: 1;
        font-weight: 700;
        background: #fff;
        height: 1rem;
        position: relative;
        > div {
          height: 50%;
          width: 100%;
          position: absolute;
          top: 50%;
          left: 50%;
          transform: translate(-50%, -50%);
          > i {
            width: 100%;
            height: 100%;
            font-size: 0.5rem;
            color: #e79191;
          }
        }
      }
      .ititle4 {
        flex: 10;
        display: flex;
        > .item {
          flex: 1;
          height: 1rem;
          background: #fff;

          .iweek {
            > .ismalltop {
              font-weight: 700;
              font-size: 0.25rem;
              line-height: 0.5rem;
            }
            >.ismallbottom{
              font-weight: 500;
              font-size: 0.28rem;
            }
          }
        }
      }
    }

    .ibody {
      display: flex;
      > .iclass {
        flex: 1;
        background: #fff;
        > div {
          height: 1.54rem;
          line-height: 1.54rem;
          font-size: 0.35rem;
          color:#e79191;
          font-weight: 600;
        }
      }
      > .iclasses {
        flex: 10;
        display: flex;
        > .iday {
          flex: 1;
          .ieveryday {
            height: 1.52rem;
            border-bottom: 0.02rem dotted #ccc;
            overflow: hidden;
            display: flex;
            flex-direction: column;
            overflow: hidden;
            .ieverydays {
              // box-shadow: 0.3rem 0.3rem 1.5rem pink;
              width: 90%;
              height: 1.5rem;
              margin: 0 auto;
              margin-top: 3px;
              > div {
                width: 100%;
                overflow: hidden;
                font-size: 0.15rem;
                flex-basis: 0.3rem;
                line-height: 0.3rem;
                text-align: center;
              }
              > .tt {
                width: 100%;
                height: 0.6rem;
                line-height: 0.6rem;
                overflow: hidden;
                flex-basis: 0.6rem;
                font-size: 0.2rem;
              }
            }
          }
        }
      }
      .van-popup__close-icon--top-left {
        top: 0.4rem !important;
        left: 0.5rem !important;
        font-size: 0.5rem;
      }
    }
  }
}
</style>
<script>
import add from "../components/add.vue";
import addcourse from "../components/addcourse.vue";
export default {
  data() {
    return {
      show: false,
      show1: false,
      frequence: 0,
      id: 1,
      idss: 1,
      classlist: [
        {
          id: 1,
          number: 1
        },
        {
          id: 2,
          number: 2
        },
        {
          id: 3,
          number: 3
        },
        {
          id: 4,
          number: 4
        },
        {
          id: 5,
          number: 5
        },
        {
          id: 6,
          number: 6
        },
        {
          id: 7,
          number: 7
        },
        {
          id: 8,
          number: 8
        }
      ],
      day: new Date().getDay(),
      timer: [],
      classes: [],
      classInfo: {},
      toSonData: {
        lessonName: "",
        teacher: "",
        location: "",
        day: "",
        startNum: "",
        endNum: "",
        startWeek: "",
        endWeek: ""
      },
      fileList: [
        {
          name: "food.jpeg",
          url:
            "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
        },
        {
          name: "food2.jpeg",
          url:
            "https://fuss10.elemecdn.com/3/63/4e7f3a15429bfda99bce42a18cdd1jpeg.jpeg?imageMogr2/thumbnail/360x360/format/webp/quality/100"
        }
      ]
    };
  },
  methods: {
    to_file() {
      console.log("file页面");
      this.$router.push("/file");
    },
    showPopup1() {
      this.show1 = true;
    },
    weekCan() {
      if (this.idss > 1) {
        this.idss = this.idss < 2 ? 1 : --this.idss;
      }
    },
    weekPlus() {
      console.log(this.idss);
      if (this.idss < 20) {
        this.idss++;
      }
    },
    setCurrentWeek() {
      this.id = this.idss;
      this.idss = this.id;
    },
    showPopup(hh) {
      let weekid = this.id;
      this.show = true;
      if (hh) {
        let itemid = hh.id;
        this.toSonData = itemid;
        localStorage.setItem("itemid", itemid);
        localStorage.setItem("weekid", weekid);
      } else {
        this.toSonData = {};
      }
    },
    //计算某一天的时间（以现在作为原点，参数为正，则计算未来的一天）
    calc(num) {
      num = num || 0;
      var d = new Date();
      var dtime = d.getTime() + 24 * 60 * 60 * 1000 * num;
      var dmonth = new Date(dtime).getMonth() + 1;
      var ddate = new Date(dtime).getDate();
      var dday = new Date(dtime).getDay();
      dmonth = dmonth < 10 ? "0" + dmonth : dmonth;
      ddate = ddate < 10 ? "0" + ddate : ddate;
      return {
        month: dmonth,
        riqi: ddate,
        zhou: dday
      };
    },
    //计算一周的时间，可通过参数（正数/负数）的传递，来获取未来/过去的第几周的周历
    shouWeek(some) {
      var nums = -this.day + 1;
      nums = nums == 1 ? -6 : nums;
      var count = nums + 7 * some;
      let timer = [];
      for (var i = count; (length = count + 7), i < length; i++) {
        var a = this.calc(i);
        timer.push(a);
      }
      this.timer = timer;
    },
    //显示上一周的课表
    lastWeek() {
      if (this.id > 1) {
        this.shouWeek(--this.frequence);
        this.id = this.id < 2 ? 1 : --this.id;
        this.showClass();
      }
    },
    //显示下一周的课表
    nextWeek() {
      if (this.id < 20) {
        this.shouWeek(++this.frequence);
        this.id++;
        this.showClass();
      }
    },
    showClass() {
      this.$axios({
        url: "https://tang1.top/qimimi/Find/CurrentWeek/All/Class",
        method: "get",
        params: {
          userid: Number(localStorage.getItem("userid")),
          currentweek: this.id
        }
      }).then(res => {
        this.classes = res.data;
      });
    },
    setweek(data) {
      this.id = data;
    },
    // 获取这节课的相关信息
    getThisClass(id) {
      let idd = id;
      this.$axios({
        url: "https://tang1.top/qimimi/Find/Class/by/classid",
        method: "get",
        params: {
          classid: idd
        }
      }).then(res => {
        if (res.data.status == 200) {
          var allInfo = res.data.classs;
          this.toSonData = {
            lessonName: allInfo.lessonName,
            teacher: allInfo.teacher,
            location: allInfo.location,
            day: allInfo.day,
            startNum: allInfo.startNum,
            endNum: allInfo.endNum,
            startWeek: allInfo.startWeek,
            endWeek: allInfo.endWeek
          };
        }
        console.log(this.toSonData);
      });
    }
  },
  filters: {
    date(data) {
      var dt = data;
      switch (dt) {
        case 0:
          dt = "周日";
          break;
        case 1:
          dt = "周一";
          break;
        case 2:
          dt = "周二";
          break;
        case 3:
          dt = "周三";
          break;
        case 4:
          dt = "周四";
          break;
        case 5:
          dt = "周五";
          break;
        case 6:
          dt = "周六";
      }
      return dt;
    }
  },
  created() {
    this.shouWeek(0);
    this.showClass();
  },
  updated() {},
  components: {
    add: add,
    update: addcourse
  }
};
</script>
