<template>
  <div class="introduce">
    <div class="iconfont icon-l-back introduce_l" @click="to_my">返回</div>
    <div class="introduce_r">
      <div @click="save_UpInfo">保存</div>
    </div>
    <div class="introduce_m">
      <!-- <textarea name id cols="30" rows="60" placeholder="介绍一下你自己吧，让小伙伴更了解你哦" v-model="feeling"></textarea> -->
      <div>
        <span>学校：</span>
        <input type="text" v-model="school" />
      </div>
      <div class="introduce_minput">
        <span>专业：</span>
        <input type="text" v-model="faculty" />
      </div>
    </div>
  </div>
</template>
<style lang="scss">
.introduce {
  width: 100%;
  position: relative;
  > .introduce_l {
    height: 1.5rem;
    line-height: 1.5rem;
    background: #fff;
    text-align: left;
    padding-left: 0.3rem;
    box-sizing: border-box;
    color: #000;
    font-size: 0.5rem;
  }
  > .introduce_r {
    height: 1.5rem;
    width: 1.7rem;
    position: absolute;
    top: 0;
    right: 0.2rem;
    > div {
      width: 100%;
      height: 0.8rem;
      line-height: 0.8rem;
      background: #e79191;
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate(-50%, -50%);
      border-radius: 0.4rem;
      font-size: 0.4rem;
      color: #fff;
    }
  }
  > .introduce_m {
    width: 100%;
    padding: 0.15rem;
    box-sizing: border-box;
    > div {
      display: flex;
      > span {
        font-size: 0.35rem;
        height: 1.2rem;
        line-height: 1.2rem;
      }
      > input {
        margin-top: 0.2rem;
        height: 1rem;
        // line-height: 1rem;
        background: none;
        font-size: 0.4rem;
        color: rgba($color: #000, $alpha: 0.6);
        border-bottom: 0.02rem solid #000;
      }
    }
    .introduce_minput {
      margin-top: 0.3rem;
    }
    textarea {
      width: 98%;
      margin: 0 auto;
      box-sizing: border-box;
      padding: 0.2rem;
      height: 2rem;
      resize: none;
      border: none;
      font-size: 0.3rem;
    }
  }
}
</style>
<script>
export default {
  data() {
    return {
      faculty: "",
      school: ""
    };
  },
  created() {},
  methods: {
    to_my() {
      this.$router.push("/my");
    },
    save_UpInfo() {
      localStorage.setItem("school", JSON.stringify(this.school));
      localStorage.setItem("faculty", JSON.stringify(this.faculty));
      this.school = "";
      this.faculty = "";
    }
  }
};
</script>