          <img :src="props.active ? icon2.active : icon2.inactive" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item to="/purchase">
        <span>社区</span>
        <template #icon="props">
          <img :src="props.active ? icon3.active : icon3.inactive" />
        </template>
      </van-tabbar-item>
      <van-tabbar-item to="/my">
        <span>设置</span>
        <template #icon="props">
          <img :src="props.active ? icon4.active : icon4.inactive" />
        </template>
      </van-tabbar-item>
    </van-tabbar>
    <transition>
      <router-view />
    </transition>
  </div>
</template>
<style lang="scss">
#app {
  font-family: "Avenir", Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-align: center;
  color: #2c3e50;
  overflow: hidden;
  background: #f5f3f3;
  .van-tabbar {
    height: 1.3rem;
    > .van-tabbar-item {
      > .van-tabbar-item__icon>img{
        height: 0.5rem;
      }
      > .van-tabbar-item__text {
        font-size: 0.3rem;
      }
    }
  }
}
