<script>
export default {
  name: "app",
  data() {
    return {
      active: 0,
      icon1: {
        inactive:
          "https://kxg-neituibao-jianli.oss-cn-beijing.aliyuncs.com/data/1587556485714.png?Expires=1902916484&OSSAccessKeyId=LTAIfo7xBETxaY0m&Signature=7DShrPm3cqw%2FAtdp0oatV%2BYO8cY%3D",
