        active:
          "https://kxg-neituibao-jianli.oss-cn-beijing.aliyuncs.com/data/1587556543721.png?Expires=1902916539&OSSAccessKeyId=LTAIfo7xBETxaY0m&Signature=UtYcheMgwNQuLYTq8V4A61pD%2F5k%3D"
      },
      bfoot: true
    };
  },
  methods: {
    checkPath(path) {
      if (/index|sort|purchase|my/.test(path)) {
        this.bfoot = true;
      }
      if (/login|reg|comment|agreement|introduce|school/.test(path)) {
        this.bfoot = false;
      }
    }
  },
  watch: {
    $route: {
      handler(current, prev) {
        this.checkPath(current.path);
      },
      immediate: true,
      deep: true
    }
  }
};
</script>
