<template>
  <div class="yijieshu">
    <div class="yi_item" v-for="item in listb" :key="item.id">
      <van-swipe-cell>
        <div class="yi_itemm">
          <div class="yi_itemm_to">
            <h4>{{item.name}}</h4>
          </div>
          <div class="yi_itemm_bo">
            <div class="yi_time">{{item.date}}</div>
            <div class="yi_loaction">{{item.location}}</div>
          </div>
          <!-- <van-popup v-model="show" get-container="body" round>
        <addcourse></addcourse>
          </van-popup>-->
        </div>
        <template #right>
          <van-button
            square
            text="删除"
            type="danger"
            class="delete-button"
            @click="ssdelete(item.id)"
          />
        </template>
      </van-swipe-cell>
    </div>
  </div>
</template>
<style lang="scss">
.yijieshu {
  padding: 0.3rem 0.3rem;
  padding-bottom: 1.4rem;
  .yi_item {
    width: 6.86rem;
    height: 1rem;
    line-height: 1rem;
    border: 0.02rem solid #ccc;
    background: #fafafa;
    border-radius: 0.16rem;
    box-sizing: border-box;
    margin-bottom: 0.2rem;
    padding-left: 0.01rem;
    .yi_itemm {
      height: 1rem;
      display: flex;
      flex-direction: column;
      .yi_itemm_to {
        padding-left: 0.25rem;
        h4 {
          font-size: 0.3rem;
          font-weight: 600;
          height: 0.6rem;
          line-height: 0.6rem;
          text-align: left;
        }
      }
      .yi_itemm_bo {
        display: flex;
        justify-content: space-between;
        padding: 0 0.25rem;
        box-sizing: border-box;
        .yi_time {
          height: 0.4rem;
          line-height: 0.4rem;
          font-size: 0.28rem;
        }
        .yi_loaction {
          height: 0.4rem;
          line-height: 0.4rem;
          font-size: 0.28rem;
        }
      }
    }
  }
  .goods-card {
    margin: 0;
    background-color: white;
  }
  .delete-button {
    height: 100%;
  }
}
</style>
<script>
import addcourse from "./addcourse.vue";
export default {
  data() {
    return {
      show: false,
      listb: []
    };
  },
  methods: {
    showPopup() {
      this.show = true;
    },
    getyi() {
      this.$axios({
        url: "https://tang1.top/qimimi/select/OneClock",
        method: "get",
        params: {
          userId: Number(localStorage.getItem("userid"))
        }
      }).then(res => {
        this.listb = res.data.data;
      });
    },
    ssdelete(itemid) {
      this.$axios({
        url: "https://tang1.top/qimimi/delete/Test",
        method: "post",
        params: {
          id: itemid,
          userId: Number(localStorage.getItem("userid"))
        }
      }).then(res => {
        this.getyi();
      });
    }
  },

  components: {
    addcourse: addcourse
  },
  created() {
    this.getyi();
  }
};
</script>