<template>
  <div class="cardaa" id="cardaaa">
    <div class="carda_title">
      <div class="ll">
        <!-- <i class="iconfont icon-fanhui" @click="showPopup()"></i> -->
        <i>编辑课程</i>
      </div>
      <!-- <van-button type="info" size="small" @click="addpublish">完成</van-button> -->
      <button @click="addpublish">完成</button>
    </div>
    <div class="carda_name">
      <i class="iconfont icon-shu"></i>
      <input type="text" placeholder="课程名称" v-model="uplessonName" />
    </div>
    <div class="carda_locat">
      <i class="iconfont icon-renxiang"></i>
      <input type="text" placeholder="教师名称" v-model="upteacher" />
    </div>
    <div class="carda_locat">
      <i class="iconfont icon-l-location"></i>
      <input type="text" placeholder="地点" v-model="uplocation" />
    </div>
    <!-- <div class="carda_name" v-show="!Boolean(upstartNum)">
      <i class="iconfont icon-shu"></i>
      <input type="text" placeholder="开始节数" v-model="upstartNum" />
    </div>-->
    <div class="carda_name">
      <i class="iconfont icon-shijian"></i>
      <input
        type="text"
        placeholder="开始节数"
        v-model="upstartNum"
        :readonly="!Boolean(upstartNum) ? false : 'readonly'"
      />
    </div>
    <div class="carda_locat">
      <i class="iconfont icon-shijian1"></i>
      <input
        type="text"
        placeholder="结束节数"
        v-model="upendNum"
        :readonly="!Boolean(upendNum) ? false : 'readonly'"
      />
    </div>
    <div class="carda_name">
      <i class="iconfont icon-shijian2"></i>
      <input
        type="text"
        placeholder="开始周"
        v-model="upstartWeek"
        :readonly="!Boolean(upstartWeek) ? false : 'readonly'"
      />
    </div>
    <div class="carda_locat">
      <i class="iconfont icon-tixiantanchuang"></i>
      <input
        type="text"
        placeholder="结束周"
        v-model="upendWeek"
        :readonly="!Boolean(upendWeek) ? false : 'readonly'"
      />
    </div>
    <div class="carda_name">
      <i class="iconfont icon-shijian11"></i>
      <input
        type="text"
        placeholder="时间"
        v-model="upday"
        :readonly="!Boolean(upday) ? false : 'readonly'"
      />
    </div>
  </div>
</template>
<style lang="scss">
.van-popup--center > .van-icon-cross {
  left: 0.5rem !important;
  &::before {
    color: #e79191;
    font-size: 0.5rem;
  }
}
.hide {
  display: none;
}
.cardaa {
  width: 6.5rem;
  background: #fff;
  box-sizing: border-box;
  padding: 0.2rem 0.5rem 0.4rem;
  input {
    font-size: 0.3rem;
    margin-left: 0.2rem;
    border-bottom: 0.02rem solid #ccc;
    width: 4.5rem;
    line-height: 0.6rem;
  }
  .iconfont {
    color: #e79191;
    font-size: 0.5rem;
  }
  .carda_name,
  .carda_locat {
    margin-top: 0.6rem;
    box-sizing: border-box;
  }
  .carda_title {
    display: flex;
    justify-content: space-between;
    border-bottom: 0.02rem solid #ccc;
    line-height: 0.8rem;
    > .ll {
      margin-left: 0.7rem;
      font-size: 0.4rem;
      > i {
        font-style: normal;
      }
    }
    > button {
      width: 1rem;
      height: 0.7rem;
      line-height: 0.7rem;
      padding: 0;
      border: none;
      background: #e79191;
      border-radius: 0.16rem;
      color: white;
      font-size: 0.3rem;
    }
  }
}
</style>
<script>
export default {
  data() {
    return {
      show: false,
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
      upday: "",
      uplessonName: "",
      upteacher: "",
      uplocation: "",
      upstartNum: "",
      upendNum: "",
      upstartWeek: "",
      upendWeek: "",
      list: [],
      classes: [],
      id: ""
    };
  },
  props: ["parentid"],
  watch: {
    // 监听父组件传过来的"parentid"是否已经发生改变
    parentid(newval, old) {
      if (typeof this.parentid != "object") {
        this.info(this.parentid);
      } else {
        this.uplessonName = "";
        this.upteacher = "";
        this.uplocation = "";
        this.upday = "";
        this.upstartNum = "";
        this.upendNum = "";
        this.upstartWeek = "";
        this.upendWeek = "";
      }
    }
  },
  methods: {
    info(id) {
      let idd = id;
      this.$axios({
        url: "https://tang1.top/qimimi/Find/Class/by/classid",
        method: "get",
        params: {
          classid: idd
        }
      }).then(res => {
        if (res.data.status == 200) {
          // 获得这节课的信息
          let infoArr = res.data.classs;
          this.uplessonName = infoArr.lessonName;
          this.upteacher = infoArr.teacher;
          this.uplocation = infoArr.location;
          this.upday = infoArr.day;
          this.upstartNum = infoArr.startNum;
          this.upendNum = infoArr.endNum;
          this.upstartWeek = infoArr.startWeek;
          this.upendWeek = infoArr.endWeek;
        }
      });
      this.uplessonName = "";
      this.upteacher = "";
      this.uplocation = "";
      this.upday = "";
      this.upstartNum = "";
      this.upendNum = "";
      this.upstartWeek = "";
      this.upendWeek = "";
    },
    addpublish() {
      let that = this;
      // if (this.uplessonName != null) {
      if (typeof this.parentid != "object") {
        let id = that.parentid;
        console.log(id);
        that
          .$axios({
            url: "https://tang1.top/qimimi/Update/Class/by/Classid",
            method: "post",
            data: {
              lessonName: this.uplessonName,
              teacher: this.upteacher,
              location: this.uplocation,
              day: this.upday,
              startNum: this.upstartNum,
              endNum: this.upendNum
            },
            params: {
              id: id
            }
          })
          .then(res => {
            document.getElementById("cardaaa").classList.add("hide");
            console.log(res);
          });
      } else {
        this.$axios({
          url: "https://tang1.top/qimimi/Add/Class",
          method: "post",
          data: {
            lessonName: this.uplessonName,
            teacher: this.upteacher,
            location: this.uplocation,
            day: this.upday,
            startNum: this.upstartNum,
            endNum: this.upendNum,
            startWeek: this.upstartWeek,
            endWeek: this.upendWeek
          },
          params: {
            userid: Number(localStorage.getItem("userid"))
          }
        }).then(res => {
          console.log(res);
        });
      }
    },
    showPopup() {
      console.log("应该遮到了");
      this.show = false;
    }
  },
  created() {},
  mounted() {
    if (typeof this.parentid != "object") {
      this.info(this.parentid);
    }
  },
  updated() {}
};
</script>
