<template>
  <div class="beikao">
    <div class="item" v-for="item in lista" :key="item.id">
      <van-swipe-cell>
        <div class="itemm">
          <div class="item_left">{{item.clock}}天</div>
          <div class="item_right">
            <h4>{{item.name}}</h4>
            <div class="item_time">{{item.date}}</div>
            <div class="item_loaction">{{item.location}}</div>
          </div>
        </div>
        <template #right>
          <van-button
            square
            text="删除"
            type="danger"
            class="delete-button"
            @click="ssdelete(item.id)"
          />
        </template>
      </van-swipe-cell>
    </div>
  </div>
</template>
<style lang="scss">
.beikao {
  padding: 0.3rem 0.3rem;
  padding-bottom: 1.4rem;
  .item {
    width: 6.86rem;
    height: 1rem;
    margin-bottom: 0.2rem;
    border: 0.02rem solid #ccc;
    border-radius: 0.16rem;
    background: #fafafa;
    .itemm {
      display: flex;
    }
    .item_left {
      background: #e79191;
      width: 0.8rem;
      height: 1rem;
      line-height: 1rem;
      text-align: center;
      border-top-left-radius: 0.16rem;
      border-bottom-left-radius: 0.16rem;
    }
    .item_right {
      position: relative;
      width: 6.26rem;
      height: 0.96rem;
      h4 {
        font-size: 0.3rem;
        font-weight: 600;
        position: absolute;
        left: 0.2rem;
        top: 0.2rem;
      }
      .item_time {
        position: absolute;
        left: 0.2rem;
        top: 0.6rem;
        font-size: 0.28rem;
        height: 0.4rem;
        line-height: 0.4rem;
      }
      .item_loaction {
        position: absolute;
        right: 0.2rem;
        top: 0.6rem;
        font-size: 0.28rem;
        height: 0.4rem;
        line-height: 0.4rem;
      }
    }
  }
  .goods-card {
    margin: 0;
    background-color: white;
  }

  .delete-button {
    height: 100%;
  }
}
</style>
<script>
import addcourse from "./addcourse.vue";
export default {
  data() {
    return {
      lista: [],
      toId: localStorage.getItem("addId"),
      pageNumber: 1
    };
  },
  methods: {
    showPopup() {
      this.show = true;
    },
    showTest() {
      let that = this;
      let pageNumber = this.pageNumber;
      that
        .$axios({
          url:
            "https://tang1.top/qimimi/all/test?userId=" +
            Number(localStorage.getItem("userid")),
          method: "post",
          data: {
            pageNumber: 1
          }
        })
        .then(res => {
          if (res.data.status == 200) {
            let brr = res.data.data;
            let crr = [];
            for (let i = 0; i < brr.length; i++) {
              if (brr[i].clock > -1) {
                crr.push(brr[i]);
              }
            }
            that
              .$axios({
                url:
                  "https://tang1.top/qimimi/all/test?userId=" +
                  Number(localStorage.getItem("userid")),
                method: "post",
                data: {
                  pageNumber: 2
                }
              })
              .then(res => {
                if (res.data.status == 200) {
                  let drr = res.data.data;
                  for (let i = 0; i < brr.length; i++) {
                    if (drr[i].clock > -1) {
                      crr.push(drr[i]);
                    }
                  }
                }
              });
            if (crr.length < 10) {
              that
                .$axios({
                  url:
                    "https://tang1.top/qimimi/all/test?userId=" +
                    Number(localStorage.getItem("userid")),
                  method: "post",
                  data: {
                    pageNumber: 3
                  }
                })
                .then(res => {
                  if (res.data.status == 200) {
                    let err = res.data.data;
                    for (let i = 0; i < brr.length; i++) {
                      if (err[i].clock > -1) {
                        crr.push(err[i]);
                      }
                    }
                    console.log(crr);
                  }
                });
            }
            that.lista = crr;
          }
        });
    },
    ssdelete(itemid) {
      this.$axios({
        url: "https://tang1.top/qimimi/delete/Test",
        method: "post",
        params: {
          id: itemid,
          userId: Number(localStorage.getItem("userid"))
        }
      }).then(res => {
        this.showTest();
      });
    }
  },
  created() {
    this.showTest();
  }
};
</script>
