<template>
  <div class="carda">
    <div class="carda_title">
      <div class="ll">
        <i>添加考试</i>
      </div>
      <!-- <van-button type="info" size="small" @click="sspublish">完成</van-button> -->
      <button @click="sspublish">完成</button>
    </div>
    <div class="carda_name">
      <i class="iconfont icon-shu"></i>
      <input type="text" placeholder="考试名称" v-model="sscoursename" />
    </div>
    <div class="carda_locat">
      <i class="iconfont icon-l-location"></i>
      <input type="text" placeholder="地点，备注" v-model="sslocation" />
    </div>
    <div class="carda_time etime_picker">
      <i class="iconfont icon-l-alarmClock"></i>
      <!-- <input type="text" placeholder="2020-02-29 13:00" v-model="ssdate" /> -->
      <div>
        <el-date-picker
          v-model="ssdate"
          type="datetime"
          placeholder="选择日期时间"
          default-time="12:00:00"
          format="yyyy-MM-dd HH:mm"
          value-format="yyyy-MM-dd HH:mm"
        ></el-date-picker>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
.popper-class {
  z-index: 6000 !important;
}
.carda {
  height: 5.5rem;
  width: 6.5rem;
  background: #fff;
  box-sizing: border-box;
  padding: 0.2rem 0.5rem 0.4rem;
  input {
    font-size: 0.3rem;
    margin-left: 0.2rem;
    border-bottom: 0.02rem solid #ccc;
    width: 4.5rem;
    line-height: 0.6rem;
  }
  .iconfont {
    color: #e79191;
    font-size: 0.5rem;
  }
  .carda_name,
  .carda_locat,
  .carda_time {
    margin-top: 0.6rem;
    box-sizing: border-box;
  }
  .etime_picker {
    display: flex;
    padding-left: 0.1rem;
  }
  .carda_title {
    display: flex;
    justify-content: space-between;
    border-bottom: 0.02rem solid #ccc;
    line-height: 0.8rem;
    > .ll {
      margin-left: 0.8rem;
      > i {
        font-style: normal;
        font-weight: 500;
        font-size: 0.4rem;
      }
    }
    > button {
      width: 1rem;
      height: 0.7rem;
      line-height: 0.7rem;
      padding: 0;
      border: none;
      background: #e79191;
      border-radius: 0.16rem;
      color:white;
      font-size: 0.3rem;
    }
  }
  .carda_name {
    .icon-shuben {
      color: rgb(205, 138, 50);
    }
  }
  .carda_locat {
    .icon-datelocation {
      color: rgb(66, 7, 90);
    }
  }
  .carda_time {
    .icon-clock {
      color: rgb(202, 74, 96);
      border-bottom: 0.02rem solid #ccc;
    }
  }
}
</style>
<script>
export default {
  data() {
    return {
      currentDate: new Date(),
      sscoursename: "",
      sslocation: "",
      ssdate: "",
      toSort: 1
    };
  },
  methods: {
    sspublish() {
      console.log(this.ssdate);
      this.$axios({
        url: "https://tang1.top/qimimi/insert/Test",
        method: "post",
        data: {
          name: this.sscoursename,
          location: this.sslocation,
          date: this.ssdate,
          userId: Number(localStorage.getItem("userid"))
        }
      }).then(res => {
        console.log(res)
        if (res.status == 200) {
          console.log("添加成功啦  哈哈哈哈");
          let to = ++this.toSort;
          localStorage.setItem("addId", to);
        }
      });
    }
  }
};
</script>
