 <template>
  <div class="add">
    <div class="plus">
      <a class="van-icon van-icon-plus" @click="po"></a>
    </div>
    <!-- 遮罩层 -->
    <div class="cover hide"></div>
    <!-- 遮罩层上面的显示层 -->
    <div class="modal hide">
      <div class="carda_title">
        <div class="ll">
          <i class="iconfont icon-fanhui" @click="popdown"></i>
          <i class="add_Title">添加课程</i>
        </div>
        <button @click="addSuccess">完成</button>
      </div>
      <div class="carda_name form_name">
        <i class="iconfont icon-shu"></i>
        <input
          type="text"
          placeholder="课程名称"
          v-model="iilessonName"
          class="span_1"
          @blur="check_input1"
        />
        <div class="span1_info hide">
          <span>课程名称不能为空</span>
        </div>
      </div>
      <div class="carda_locat form_name">
        <i class="iconfont icon-renxiang"></i>
        <input
          type="text"
          placeholder="教师名称"
          v-model="iiteacher"
          class="span_2"
          @blur="check_input2"
        />
        <div class="span2_info hide">
          <span>教师名称不能为空</span>
        </div>
      </div>
      <div class="carda_locat form_name">
        <i class="iconfont icon-l-location"></i>
        <input
          type="text"
          placeholder="地点"
          v-model="iilocation"
          class="span_3"
          @blur="check_input3"
        />
        <div class="span3_info hide">
          <span>地点不能为空</span>
        </div>
      </div>
      <div class="carda_name">
        <i class="iconfont icon-shijian"></i>
        <!-- <input type="text" placeholder="开始节数" v-model="iistartNum" /> -->
        <el-select v-model="iistartNum" slot="prepend" placeholder="开始节数">
          <el-option label="1" value="1"></el-option>
          <el-option label="2" value="2"></el-option>
          <el-option label="3" value="3"></el-option>
          <el-option label="4" value="4"></el-option>
          <el-option label="5" value="5"></el-option>
          <el-option label="6" value="6"></el-option>
          <el-option label="7" value="7"></el-option>
          <el-option label="8" value="8"></el-option>
        </el-select>
      </div>
      <div class="carda_locat">
        <i class="iconfont icon-shijian1"></i>
        <el-select v-model="iiendNum" slot="prepend" placeholder="结束节数">
          <el-option label="1" value="1"></el-option>
          <el-option label="2" value="2"></el-option>
          <el-option label="3" value="3"></el-option>
          <el-option label="4" value="4"></el-option>
          <el-option label="5" value="5"></el-option>
          <el-option label="6" value="6"></el-option>
          <el-option label="7" value="7"></el-option>
          <el-option label="8" value="8"></el-option>
        </el-select>
      </div>
      <div class="carda_name">
        <i class="iconfont icon-shijian2"></i>
        <el-select v-model="iistartWeek" placeholder="开始周">
          <el-option
            v-for="item in week_options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="carda_locat">
        <i class="iconfont icon-tixiantanchuang"></i>
        <el-select v-model="iiendWeek" placeholder="结束周">
          <el-option
            v-for="item in week_options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
      <div class="carda_name">
        <i class="iconfont icon-shijian11"></i>
        <el-select v-model="iiday" placeholder="时间">
          <el-option
            v-for="item in time_options"
            :key="item.value"
            :label="item.label"
            :value="item.value"
          ></el-option>
        </el-select>
      </div>
    </div>
  </div>
</template>
<style lang="scss">
.add {
  .plus {
    position: fixed;
    top: 0;
    right: 0.2rem;
    font-size: 0.6rem;
    line-height: 1.6rem;
    z-index: 2;
    .van-icon-plus {
      color: #fff;
    }
  }
  .cover {
    position: fixed;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    /*上下左右设置都为0*/
    background-color: rgba(20, 19, 19, 0.658);
    z-index: 99;
  }

  .modal {
    // position: absolute;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    z-index: 100;
    border-radius: 0.5rem;
    height: 12rem;
    width: 6.5rem;
    background: #fff;
    box-sizing: border-box;
    padding: 0.2rem 0.5rem 0.4rem;

    .el-input__inner {
      border: 1px solid #e79191;
    }

    .add_Title {
      margin-left: 0.5rem;
      font-size: 0.4rem;
      font-style: normal;
    }
    input {
      font-size: 0.3rem;
      margin-left: 0.2rem;
      border-bottom: 0.02rem solid #ccc;
      width: 4.5rem;
      line-height: 0.6rem;
    }
    .iconfont {
      // color: rgb(57, 90, 151);
      color: #e79191;
      font-size: 0.5rem;
    }

    .carda_name,
    .carda_locat {
      margin-top: 0.5rem;
      box-sizing: border-box;
    }
    .form_name {
      position: relative;
      > div {
        position: absolute;
        top: 35px;
        left: 50px;
        color: rgb(214, 105, 105);
      }
    }
    .carda_title {
      display: flex;
      justify-content: space-between;
      border-bottom: 0.02rem solid #ccc;
      line-height: 0.8rem;
      padding-left: 0.15rem;
      > button {
        width: 1rem;
        height: 0.7rem;
        line-height: 0.7rem;
        padding: 0;
        border: none;
        background: #e79191;
        border-radius: 0.16rem;
        color: white;
        font-size: 0.3rem;
      }
    }

    .carda_name {
      .icon-shuben {
        color: rgb(205, 138, 50);
      }
    }

    .carda_locat {
      .icon-datelocation {
        color: rgb(66, 7, 90);
      }
    }
  }
  // 设置x的位置为右上角
  .close {
    float: right;
    font-size: 16px;
    font-size: 0.8rem;
  }
  .hide {
    display: none;
  }
}
.error {
  border: 1px solid rgb(214, 105, 105) !important;
  border-radius: 8px;
}
</style>
<script>
export default {
  data() {
    return {
      iiday: "",
      iiendNum: "",
      iiendWeek: "",
      iilessonName: "",
      iilocation: "",
      iistartNum: "",
      iistartWeek: "",
      iiteacher: "",
      week_options: [
        {
          value: "1",
          label: "第一周"
        },
        {
          value: "2",
          label: "第二周"
        },
        {
          value: "3",
          label: "第三周"
        },
        {
          value: "4",
          label: "第四周"
        },
        {
          value: "5",
          label: "第五周"
        },
        {
          value: "6",
          label: "第六周"
        },
        {
          value: "7",
          label: "第七周"
        },
        {
          value: "8",
          label: "第八周"
        },
        {
          value: "9",
          label: "第九周"
        },
        {
          value: "10",
          label: "第十周"
        },
        {
          value: "11",
          label: "第十一周"
        },
        {
          value: "12",
          label: "第十二周"
        },
        {
          value: "13",
          label: "第十三周"
        },
        {
          value: "14",
          label: "第十四周"
        },
        {
          value: "15",
          label: "第十五周"
        },
        {
          value: "16",
          label: "第十六周"
        },
        {
          value: "17",
          label: "第十七周"
        },
        {
          value: "18",
          label: "第十八周"
        },
        {
          value: "19",
          label: "第十九周"
        },
        {
          value: "20",
          label: "第二十周"
        }
      ],
      time_options: [
        {
          value: "星期一",
          label: "星期一"
        },
        {
          value: "星期二",
          label: "星期二"
        },
        {
          value: "星期三",
          label: "星期三"
        },
        {
          value: "星期四",
          label: "星期四"
        },
        {
          value: "星期五",
          label: "星期五"
        },
        {
          value: "星期六",
          label: "星期六"
        },
        {
          value: "星期日",
          label: "星期日"
        }
      ]
    };
  },
  methods: {
    error1() {
      this.$message({
        showClose: true,
        message: "您还有必填内容未完成",
        type: "error",
        offset: 150
      });
    },
    check_input1() {
      if (!this.iilessonName) {
        document.getElementsByClassName("span_1")[0].classList.add("error");
        document
          .getElementsByClassName("span1_info")[0]
          .classList.remove("hide");
      } else {
        document.getElementsByClassName("span_1")[0].classList.remove("error");
        document.getElementsByClassName("span1_info")[0].classList.add("hide");
      }
    },
    check_input2() {
      if (!this.iiteacher) {
        document.getElementsByClassName("span_2")[0].classList.add("error");
        document
          .getElementsByClassName("span2_info")[0]
          .classList.remove("hide");
      } else {
        document.getElementsByClassName("span_2")[0].classList.remove("error");
        document.getElementsByClassName("span2_info")[0].classList.add("hide");
      }
    },
    check_input3() {
      if (!this.iilocation) {
        document.getElementsByClassName("span_3")[0].classList.add("error");
        document
          .getElementsByClassName("span3_info")[0]
          .classList.remove("hide");
      } else {
        document.getElementsByClassName("span_3")[0].classList.remove("error");
        document.getElementsByClassName("span3_info")[0].classList.add("hide");
      }
    },
    po() {
      document.getElementsByClassName("cover")[0].classList.remove("hide");
      document.getElementsByClassName("modal")[0].classList.remove("hide");
    },
    popdown() {
      document.getElementsByClassName("cover")[0].classList.add("hide");
      document.getElementsByClassName("modal")[0].classList.add("hide");
    },
    addSuccess() {
      // 课程名称，教师名称，地点以及其他内容不能为空
      let that = this;
      let a = this.iilessonName;
      let b = this.iiteacher;
      let c = this.iilocation;
      let d = this.iistartNum;
      let e = this.iiendNum;
      let f = this.iistartWeek;
      let g = this.iiendWeek;
      let h = this.iiday;
      if (!a) {
        this.check_input1();
      } else {
        if (!b) {
          this.check_input2();
        } else {
          if (!c) {
            this.check_input3();
          } else {
            if (!d || !e || !f || !g || !h) {
              that.error1();
            } else {
              this.$axios({
                url: "https://tang1.top/qimimi/Add/Class",
                method: "post",
                data: {
                  lessonName: this.iilessonName,
                  teacher: this.iiteacher,
                  location: this.iilocation,
                  day: this.iiday,
                  startNum: this.iistartNum,
                  endNum: this.iiendNum,
                  startWeek: this.iistartWeek,
                  endWeek: this.iiendWeek
                },
                params: {
                  userid: Number(localStorage.getItem("userid"))
                }
              }).then(res => {
                if (res.status == 200) {
                  this.popdown();
                  (this.iilessonName = ""),
                    (this.iiteacher = ""),
                    (this.iilocation = ""),
                    (this.iiday = ""),
                    (this.iistartNum = ""),
                    (this.iiendNum = ""),
                    (this.iistartWeek = ""),
                    (this.iiendWeek = "");
                }
              });
            }
          }
        }
      }
    }
  },
  mounted() {}
};
</script>
